section {
  padding-bottom: 95px;
  min-height: 460px;
  display: block;
  position: relative !important;
  width: 100%;
}

section .sect_cnt {
  clear: both;
  height: 100%;
  display: block;
  width: 1050px;
  margin: 0 auto;
}

.panel.slider {
  background: rgba(0, 0, 0, 0.5);
}

.arrow.tonext {
  border-radius: 35% 35% 0 0;
  background: url(../img/tonext.png) bottom center no-repeat;
  position: absolute;
  bottom: 0px;
  left: 50%;
  margin-left: -79px;
  padding: 32px 22px 31px;
  font-family: 'museo_sans_cyrl900';
  font-size: 11px;
  text-transform: uppercase;
  text-decoration: none;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  -webkit-animation: color-change 3s infinite;
  -moz-animation: color-change 3s infinite;
  -o-animation: color-change 3s infinite;
  -ms-animation: color-change 3s infinite;
  animation: color-change 3s infinite;
  color: white;
}

@-webkit-keyframes color-change {
  0% {
    text-shadow: 0 0 0px rgba(209, 42, 74, 0);
  }

  50% {
    text-shadow: 0 0 10px rgba(209, 42, 74, 1);
    box-shadow: 0 0 10px 0px rgba(209, 42, 74, 0.5);
  }

  100% {
    text-shadow: 0 0 0px rgba(209, 42, 74, 0);
  }
}

@-moz-keyframes color-change {
  0% {
    text-shadow: 0 0 0px rgba(209, 42, 74, 0);
  }

  50% {
    text-shadow: 0 0 10px rgba(209, 42, 74, 1);
    box-shadow: 0 0 10px 0px rgba(209, 42, 74, 0.5);
  }

  100% {
    text-shadow: 0 0 0px rgba(209, 42, 74, 0);
  }
}

@-ms-keyframes color-change {
  0% {
    text-shadow: 0 0 0px rgba(209, 42, 74, 0);
  }

  50% {
    text-shadow: 0 0 10px rgba(209, 42, 74, 1);
    box-shadow: 0 0 10px 0px rgba(209, 42, 74, 0.5);
  }

  100% {
    color: white;
  }
}

@-o-keyframes color-change {
  0% {
    text-shadow: 0 0 0px rgba(209, 42, 74, 0);
  }

  50% {
    text-shadow: 0 0 10px rgba(209, 42, 74, 1);
    box-shadow: 0 0 10px 0px rgba(209, 42, 74, 0.5);
  }

  100% {
    text-shadow: 0 0 0px rgba(209, 42, 74, 0);
  }
}

@keyframes color-change {
  0% {
    text-shadow: 0 0 0px rgba(209, 42, 74, 0);
  }

  50% {
    text-shadow: 0 0 10px rgba(209, 42, 74, 1);
    box-shadow: 0 0 10px 0px rgba(209, 42, 74, 0.5);
  }

  100% {
    text-shadow: 0 0 0px rgba(209, 42, 74, 0);
  }
}

.arrow.tonext:after {
  border-right: 1px solid #6f6f77;
  border-bottom: 1px solid #6f6f77;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  content: '';
  width: 15px;
  height: 15px;
  position: absolute;
  margin: auto;
  top: 25px;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-transition: all ease-out 0.3s;
  -moz-transition: all ease-out 0.3s;
  -o-transition: all ease-out 0.3s;
  transition: all ease-out 0.3s;
}

.arrow.tonext:hover:after {
  top: 35px;
}

.arrow.toprev:hover:after {
  top: -30px;
}

.arrow.toprev {
  background: url(../img/toprev.png) bottom center no-repeat;
  position: absolute;
  top: 0px;
  left: 50%;
  z-index: 100;
  margin-left: -79px;
  padding: 40px 47px 23px;
  font-family: 'museo_sans_cyrl900';
  font-size: 11px;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

.arrow.toprev:after {
  border-right: 1px solid #6f6f77;
  border-bottom: 1px solid #6f6f77;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(225deg);
  content: '';
  width: 15px;
  height: 15px;
  position: absolute;
  margin: auto;
  top: -20px;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-transition: all ease-out 0.3s;
  -moz-transition: all ease-out 0.3s;
  -o-transition: all ease-out 0.3s;
  transition: all ease-out 0.3s;
}

.features {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  display: -ms-flex;
