  background: url(../img/pricebg.png) top center no-repeat;
  font-size: 13px;
  padding: 20px 17px 15px;
  font-family: 'museo_sans_cyrl500';
  position: absolute;
  top: -15px;
  right: 20px;
}

.step1_comb,
.step1_kpp {
  display: none;
}

.step1 div {
  opacity: 1;
  position: relative;
}

.step1 div div {
  position: absolute;
}

.jarrow {
  background: url(../img/jarrow.png) center right no-repeat;
  width: 35px;
}

#step1.modal_div {
  width: 740px;
  background: #fff;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -400px;
  display: none;
  opacity: 0;
  z-index: 1001;
  padding: 40px 30px;
  color: black;
}

.modal_close {
  width: 21px;
  height: 21px;
  position: absolute;
  top: 20px;
  right: 25px;
  cursor: pointer;
  display: block;
}

#overlay {
  z-index: 1000;
  position: fixed;
  background-color: #000;
  opacity: 0.8;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  cursor: pointer;
  display: none;
}

#overlay2 {
  z-index: 1000;
  position: fixed;
  background-color: #000;
  opacity: 0.3;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  cursor: pointer;
  display: none;
}

.type_blocks {
  display: block;
  width: 215px;
  text-align: center;
  position: relative;
  float: left;
  margin: 0 15px 0px;
  padding-bottom: 25px;
}

.type_blocks a {
  text-decoration: none;
}

.imgicon {
  left: -18px;
  position: relative;
  bottom: 0px;
  transition: all 0.4s ease 0s;
}

.type_blocks h2 {
  font-family: 'museo_sans_cyrl900';
  font-size: 20px;
  text-transform: uppercase;
  color: #d12a4a;
  margin-top: 15px;
  border-bottom: 2px dotted rgb(209, 42, 74);
  display: inline-block;
  padding-bottom: 5px;
}

.type_blocks h2:after {
  content: '';
  width: 1%;
  left: 0;
  right: 0;
  position: absolute;
  bottom: 0;
  transition: all 0.4s ease 0s;
  height: 4px;
  z-index: -1;
  margin: 0 auto;
  background: none repeat scroll 0 0 #fff;
}

.type_blocks:hover h2:after {
  background: none repeat scroll 0 0 #c7c1b8;
  width: 100%;
}

.type_blocks:hover .imgicon {
  bottom: 10px;
}

.step_block {
  margin-bottom: 24px;
  font-family: 'museo_sans_cyrl900';
