  font-family: 'museo_sans_cyrl500';
  display: inline-block;
  text-align: center;
  border-top: 1px solid #c7c1b8;
  width: 705px;
  margin: 0 15px;
  padding-top: 25px;
  font-size: 16px;
}

.sub_desc {
  margin-top: 15px;
